.circle-preloader {
  display: block;
  width: 50px;
  height: 50px;
  border: 4px solid #444;
  border-bottom-color: #888;

  border-radius: 50%;

  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  margin: auto;

  animation: spin 5s infinite linear;
  z-index: 999;
}

@keyframes spin {
  100% {
    transform: rotate(360deg);
  }
}
